import React from "react";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
// import FormControl from "@material-ui/core/FormControl";
// import Select from "@material-ui/core/Select";
// import MenuItem from "@material-ui/core/MenuItem";
// import InputLabel from "@material-ui/core/InputLabel";
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Tooltip from "@material-ui/core/Tooltip";
import Hidden from "@material-ui/core/Hidden";
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import Paper from '@material-ui/core/Paper';
import TableContainer from '@material-ui/core/TableContainer';
// @material-ui/icons
import Dvr from "@material-ui/icons/Dvr";
import PlayArrow from "@material-ui/icons/PlayArrow";
import Stop from "@material-ui/icons/Stop";
import Delete from "@material-ui/icons/Delete";
import PlayForWork from "@material-ui/icons/PlayForWork";
import Close from "@material-ui/icons/Close";
import Remove from "@material-ui/icons/Remove";
import Add from "@material-ui/icons/Add";
// import Showdetail from "@material-ui/icons/List";

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";

// exp components
import NBrootsPagination from "components/@NBrootsPagination/NBrootsPagination.js";
import NBrootsDialogProcess from "components/@NBrootsDialogProcess/NBrootsDialogProcess.js";
import NBrootsMsgBox from "components/@NBrootsMsgBox/NBrootsMsgBox.js";
import NBrootsConfirm from "components/@NBrootsConfirm/NBrootsConfirm.js";

import { BASE_URL } from 'variables/config.js';
import Service from "utils/service/service.js"

import styles from "assets/jss/material-dashboard-pro-react/views/TerminalList/terminalListStyle.js";

export default function TerminalList(props) {
  //样式
  const useStyles = makeStyles(styles);
  const classes = useStyles();

  //進捗画面
  const [isLoading, setIsLoading] = React.useState(false);
  //Msg画面用情報             msgInfo     : [表示FLG, Msg, Type:"info","success","warning","danger","primary","rose"]    
  const [msgInfo, setMsgInfo] = React.useState([false, '', 'info']);
  //Confirm画面用情報         confirmInfo : [表示FLG, Msg, returnVal ]    
  const [confirmInfo, setConfirmInfo] = React.useState([false, '', { "opt": '', "id": '', "result": 0 }]);

  //ページジャンプとソート用    pageInfos   : [記録数、ページサイズ,当ページ番号]
  const [pageInfos, setPageInfos] = React.useState([0, 10, 1]);
  //明細情報
  const [lstContent, setLstContent] = React.useState([]);

  //場所追加画面関連情報
  const [addGroupNameDialogOpen, setAddGroupNameDialogOpen] = React.useState(false);

  // effect instead of componentDidMount, componentDidUpdate and componentWillUnmount
  React.useEffect(() => {
    //進捗画面
    setIsLoading(true);
    //情報のロード
    formatLstData();

  }, []);    //[] 忘れないよう…　初期化



  //APIJSON　→　画面用JSON（FLGやERRMSGなど）
  // const formatLstData = async () => {
  const formatLstData = () => {

    var url = BASE_URL + "/api/terminals_list.json";
    var data = {
    };

    Service(url, data, "GET").then(resp => {
      console.log(resp);
      // 正常系
      if (resp.status === 0) {
        var info = resp.infos;
        //ページジャンプとソート用    pageInfos   : [記録数、ページサイズ,当ページ番号]
        setPageInfos([info.count, info.pageSize, info.pageNo]);
        //Data情報の処理
        var lst = info.dataRows;
        lst.map(item => {
          return {
            ...item,
            //追加属性
            isEdit: false,
            chkResultName: false,
          }
        });
        setLstContent(lst);
      } else {
        //その他APIエラー      
      }
      setIsLoading(false);
    }).catch(resp => {
      //接続エラーなど
      setIsLoading(false);
      setMsgInfo([true, resp, 'danger']);
    });
  };

  //改ページ処理
  // pageInfos   : [記録数、ページサイズ,当ページ番号]
  const handleChangePageStart = (page) => {

    if (page.disabled) return;
    if (page.text === pageInfos[2]) return;

    setIsLoading(true);

    setTimeout(() => {

      if (page.text === 'Prev') {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] - 1]);
      } else if (page.text === 'Next') {
        setPageInfos([pageInfos[0], pageInfos[1], pageInfos[2] + 1]);
      } else {
        setPageInfos([pageInfos[0], pageInfos[1], page.text]);
      }

      setIsLoading(false);

    }, 500);
  };

  //設置追加ボタン押す
  const handleClickAddGroupNameDialogOpen = () => {
    setAddGroupNameDialogOpen(true);
  };

  //設置追加画面閉じる
  const handleClickAddGroupNameDialogClose = () => {
    setAddGroupNameDialogOpen(false);
  };

  //端末追加ボタン押す
  const handleClickAddDialogOpen = () => {
    props.history.push('/TerminalList/Add')
    // setAddDialogOpen(true);
  };

  //起動ボタン押す
  const handleClickStartBtn = (obj) => {

    var index = lstContent.indexOf(obj);

    setConfirmInfo([true, "起動しますか？",
      {
        "opt": "start",
        "id": index,
        "result": 0
      }
    ]);
  };

  //停止ボタン押す
  const handleClickStopBtn = (obj) => {
    var index = lstContent.indexOf(obj);

    setConfirmInfo([true, "停止しますか？",
      {
        "opt": "stop",
        "id": index,
        "result": 0
      }
    ]);
  };

  //同期ボタン押す
  const handleClickSynchronizeBtn = (obj) => {

    var index = lstContent.indexOf(obj);

    setConfirmInfo([true, "同期しますか？",
      {
        "opt": "synchronize",
        "id": index,
        "result": 0
      }
    ]);

  };

  //変更ボタン押す
  const handleClickEditDialogOpen = (obj) => {
    props.history.push('/TerminalList/Detail/' + obj.id);
  };

  //画面動作 DEL
  const handleClickStartDel = (obj) => {

    var index = lstContent.indexOf(obj);

    setConfirmInfo([true, "削除、よろしいでしょうか？",
      {
        "opt": "del",
        "id": index,
        "result": 0
      }
    ]);
  };





  //確認メッセージ後の処理
  React.useEffect(() => {

    //起動処理
    const doStart = () => {
      setIsLoading(true);
      console.log("Start : " + confirmInfo[2].id);
      setTimeout(() => {
        console.log(JSON.stringify(confirmInfo[2].id));

        setIsLoading(false);
        setMsgInfo([true, '端末へ情報の送信は成功しました。', 'success']);

      }, 1000);
    }

    //削除処理
    function doDel(){
      setIsLoading(true);
      console.log("Del : " + confirmInfo[2].id);
      setTimeout(() => {

        formatLstData();
        setMsgInfo([true, '削除成功しました。', 'success']);

      }, 1000);
    }

    //起動確認結果
    if (confirmInfo[2].opt === "del" && confirmInfo[2].result === 1) {
      doDel();
    //起動確認結果
    } else if (confirmInfo[2].opt === "start" && confirmInfo[2].result === 1) {
      doStart();
    //停止確認結果
    } else if (confirmInfo[2].opt === "stop" && confirmInfo[2].result === 1) {
      doStart();
    //同期確認結果
    } else if (confirmInfo[2].opt === "synchronize" && confirmInfo[2].result === 1) {
      doStart();
    }

  }, [confirmInfo]);







  return (
    <Box>
      <Grid container spacing={1}>
        {/* 検索部 */}
        <Grid item xs={6} sm={6} className={classes.searchBox}>

        </Grid>
        {/* 操作ボタン部 */}
        <Grid item xs={6} sm={6} className={classes.optBox}>
          <Hidden smDown>
            <Button onClick={handleClickAddGroupNameDialogOpen} color="warning">場所追加</Button>
            <Button onClick={handleClickAddDialogOpen} color="warning">端末追加</Button>
          </Hidden>
          {/* 携帯の場合、ボタンの幅を縮小する */}
          <Hidden mdUp>
            <Button style={{ width: "70px" }} onClick={handleClickAddGroupNameDialogOpen} color="warning">場所追加</Button>
            <Button style={{ width: "70px" }} onClick={handleClickAddDialogOpen} color="warning">端末追加</Button>
          </Hidden>
        </Grid>
        {/* ページジャンプ */}
        <Grid item xs={12} className={classes.pageJumpBox}>
          {/* [記録数、ページサイズ,当ページ番号] */}
          <NBrootsPagination
            count={pageInfos[0]}
            pagesize={pageInfos[1]}
            current={pageInfos[2]}
            onClick={handleChangePageStart}
            color="warning"
          />
        </Grid>
      </Grid>

      <Box xs={12} className={classes.tableBox}>
        <Paper className={classes.tableCardBody}>
          <TableContainer className={classes.table}>
            <Table stickyHeader>
              <TableHead>
                <TableRow>
                  <Hidden smDown>
                    <TableCell className={classes.tableHead} style={{ width: "10%" }}>企業</TableCell>
                  </Hidden>
                  <TableCell className={classes.tableHead} style={{ width: "10%" }}>設置場所名</TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "15%" }}>端末情報</TableCell>
                  <Hidden smDown>
                    <TableCell className={classes.tableHead} style={{ width: "25%" }}>プレイリスト</TableCell>
                    <TableCell className={classes.tableHead} style={{ width: "5%" }}>起動時間</TableCell>
                    <TableCell className={classes.tableHead} style={{ width: "5%" }}>停止時間</TableCell>
                  </Hidden>
                  <TableCell className={classes.tableHead} style={{ width: "10%" }}>状態</TableCell>
                  <TableCell className={classes.tableHead} style={{ width: "20%" }}>操作</TableCell>
                </TableRow>
              </TableHead>
              <TableBody className={classes.tableBody}>
                {lstContent ? lstContent.map((item, key) =>
                  <TableRow className={classes.tableRow} key={key}>
                    <Hidden smDown>
                      <TableCell className={classes.tableCellLeft} >
                        {item.corp}
                      </TableCell>
                    </Hidden>
                    <TableCell className={classes.tableCellLeft} >
                      {item.group}
                    </TableCell>
                    <TableCell className={classes.tableCellLeft} >
                      <Box>{item.place}&nbsp;&nbsp;{item.terminalInfo1}&nbsp;&nbsp;{item.terminalInfo2}</Box><Box>{item.terminalInfo3}</Box>
                    </TableCell>
                    <Hidden smDown>
                      <TableCell className={classes.tableCellLeft} >{item.playlist}</TableCell>
                      <TableCell className={classes.tableCellRight} >{item.terminalStart}</TableCell>
                      <TableCell className={classes.tableCellRight} >{item.terminalEnd}</TableCell>
                    </Hidden>
                    <TableCell className={classes.tableCell} >{item.terminalStatus}</TableCell>
                    <TableCell className={classes.tableCell} >
                      <Tooltip id="tooltip-top" title="起動" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickStartBtn(item) }} color="info">
                          <PlayArrow />
                        </Button>
                      </Tooltip>
                      <Tooltip id="tooltip-top" title="停止" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickStopBtn(item) }} color="info">
                          <Stop />
                        </Button>
                      </Tooltip>
                      <Tooltip id="tooltip-top" title="同期" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickSynchronizeBtn(item) }} color="success">
                          <PlayForWork />
                        </Button>
                      </Tooltip>
                      <Tooltip id="tooltip-top" title="詳細情報" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickEditDialogOpen(item) }} color="warning" >
                          <Dvr />
                        </Button>
                      </Tooltip>
                      <Tooltip id="tooltip-top" title="削除" placement="bottom" classes={{ tooltip: classes.tooltip }}>
                        <Button simple className={classes.actionButton} onClick={() => { handleClickStartDel(item) }} color="danger">
                          <Delete />
                        </Button>
                      </Tooltip>
                    </TableCell>
                  </TableRow>
                ) : null}
              </TableBody>
            </Table>
          </TableContainer>
        </Paper>
        {/* 設置場所名追加 */}
        <Dialog
          onClose={handleClickAddGroupNameDialogClose}
          aria-labelledby="customized-dialog-title"
          open={addGroupNameDialogOpen}
          fullWidth={true}
          maxWidth="sm"
        >
          <DialogTitle id="customized-dialog-title" className={classes.dialogHeader} onClose={handleClickAddGroupNameDialogClose}>
            設置場所名追加
            <Button
              justIcon
              className={classes.modalCloseButton}
              key="close"
              aria-label="Close"
              color="warning"
              onClick={handleClickAddGroupNameDialogClose}
              style={{ float: "right" }}
            >
              <Close className={classes.modalClose} />
            </Button>
          </DialogTitle>
          <DialogContent>
            <GridItem xs={12} sm={12} md={12} lg={12}>
              <Button color="info" round className={classes.marginRight}>
                <Add className={classes.icons} /> 設置場所追加
          </Button>
              <GridContainer>
                <GridItem xs={12} sm={10} md={10} lg={10}>
                  <CustomInput
                    id="groupName"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      type: "text",
                      value: "神奈川県設置"
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={2} md={2} lg={2}>
                  <Button color="danger" justIcon round className={classes.marginRight}>
                    <Remove className={classes.icons} />
                  </Button>
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={10} md={10} lg={10}>
                  <CustomInput
                    id="groupName"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      type: "text",
                      autoComplete: "off",
                      value: "東京設置"
                    }}
                  />
                </GridItem>
                <GridItem xs={12} sm={2} md={2} lg={2}>
                  <Button color="danger" justIcon round className={classes.marginRight}>
                    <Remove className={classes.icons} />
                  </Button>
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={10} md={10} lg={10}>
                  <CustomInput
                    id="name"
                    formControlProps={{ fullWidth: true }}
                    inputProps={{ type: "text", autoComplete: "off", value: "北海道設置" }}
                  />
                </GridItem>
                <GridItem xs={12} sm={2} md={2} lg={2}>
                  <Button color="danger" justIcon round className={classes.marginRight}>
                    <Remove className={classes.icons} />
                  </Button>
                </GridItem>
              </GridContainer>
            </GridItem>
          </DialogContent>
          <DialogActions>
            <Button round onClick={handleClickAddGroupNameDialogClose} color="warning">
              キャンセル
        </Button>
            <Button round onClick={handleClickAddGroupNameDialogClose} color="warning">
              保存
        </Button>
          </DialogActions>
        </Dialog>


        {/* 共通ダイアログ画面 */}
        <NBrootsConfirm info={confirmInfo} setInfo={setConfirmInfo} />
        <NBrootsDialogProcess open={isLoading} color="warning" />
        <NBrootsMsgBox open={msgInfo[0]} message={msgInfo[1]} color={msgInfo[2]} close
          closeNotification={() => setMsgInfo([false, msgInfo[1], msgInfo[2]])} />
      </Box>
    </Box>
  );
}
